import routes from '../routes/routes';
import { getActiveRoute } from '../routes/url-parser';
import Auth from '../data/auth';

export default class App {
  constructor({ content, drawerButton, navigationDrawer }) {
    this._content = content;
    this._drawerButton = drawerButton;
    this._navigationDrawer = navigationDrawer;
    this._setupDrawer();
    this._checkAuth();
  }

  _setupDrawer() {
    this._drawerButton.addEventListener('click', () => {
      this._navigationDrawer.classList.toggle('open');
    });

    document.body.addEventListener('click', (event) => {
      if (!this._navigationDrawer.contains(event.target) && 
          !this._drawerButton.contains(event.target)) {
        this._navigationDrawer.classList.remove('open');
      }
    });

    this._navigationDrawer.querySelectorAll('a').forEach((link) => {
      link.addEventListener('click', () => {
        this._navigationDrawer.classList.remove('open');
      });
    });
  }

  _checkAuth() {
    const token = Auth.getUserToken();
    const currentPath = window.location.hash.split('?')[0];
    const publicRoutes = ['#/login', '#/register', '#/about'];
    
    if (!token && !publicRoutes.includes(currentPath)) {
      window.location.hash = '#/login';
      return;
    }
    
    if (token && (currentPath === '#/login' || currentPath === '#/register')) {
      window.location.hash = '#/';
      return;
    }
  }

  async renderPage() {
    const url = getActiveRoute();
    const page = routes[url];

    if (!page) {
      this._content.innerHTML = `
        <section class="container">
          <div class="error">
            <i class="fas fa-exclamation-triangle"></i>
            <h2>404 - Halaman Tidak Ditemukan</h2>
            <p>Halaman yang Anda cari tidak ada.</p>
            <a href="#/" class="button primary">Kembali ke Beranda</a>
          </div>
        </section>
      `;
      return;
    }

    try {
      if (document.startViewTransition) {
        document.startViewTransition(async () => {
          this._content.innerHTML = await page.render();
          await page.afterRender();
          this._updateActiveNavLink();
          this._checkAuth();
        });
      } else {
        this._content.innerHTML = await page.render();
        await page.afterRender();
        this._updateActiveNavLink();
        this._checkAuth();
      }
    } catch (error) {
      console.error('Error rendering page:', error);
      this._content.innerHTML = `
        <section class="container">
          <div class="error">
            <i class="fas fa-exclamation-triangle"></i>
            <h2>Error Memuat Halaman</h2>
            <p>${error.message || 'Silakan coba lagi nanti'}</p>
            <a href="#/" class="button primary">Kembali ke Beranda</a>
          </div>
        </section>
      `;
    }
  }

  _updateActiveNavLink() {
    const navLinks = document.querySelectorAll('.nav-list a');
    const currentPath = window.location.hash.split('?')[0];

    navLinks.forEach((link) => {
      const linkPath = link.getAttribute('href').split('?')[0];
      if (linkPath === currentPath || (linkPath === '#/' && currentPath === '')) {
        link.classList.add('active');
      } else {
        link.classList.remove('active');
      }
    });
  }
}